* {
  margin: 0;
  padding: 0;
  font-size: 100%;
  box-sizing: border-box;
  transition: 0.3s;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  overflow-x: hidden;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
}

.item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  // cursor: pointer;
  background-repeat: no-repeat;
  background-position: fixed;
  background-attachment: center;
  background-size: 10%;
  
  &:hover {
    .button-panel {
      width: 200px;
      margin-top: 50px;
      opacity: 1;
    }

    .back-image {
      opacity: 0.25;
    }
  }
  
  .back-image {
    position: absolute;
    width: 100%;
    height: 100%;
    background-attachment: fixed;
    background-repeat: repeat;
    background-position: top left;
    -webkit-background-size: 50% 50%;
    -moz-background-size: 50% 50%;
    -o-background-size: 50% 50%;
    background-size: 50% 50%;
    z-index: 0;
    opacity: 0;

    &.formi {
      background-image: url('./images/formi.png');
    }

    &.trello-clone {
      background-image: url('./images/trello-clone.png');
    }

    &.diprella {
      background-image: url('./images/diprella.png');
    }

    &.todo {
      background-image: url('./images/todo.png');
    }

    &.portfolio {
      background-image: url('./images/portfolio.png');
    }
  }
  
  .title {
    font-size: 222%;
    color: #222d;
    z-index: 1;
  }
  
  .button-panel {
    z-index: 2;
    position: relative;
    width: 50px;
    margin-top: -30px;
    opacity: 0;
    
    .button {
      display: inline-block;
      padding: 1px 11px;
      font-size: 132%;
      background-color: #333a;
      text-align: center;
      transform: skew(30deg);
      cursor: pointer;
      
      .text {
        transform: skew(-30deg);
      }
    }
    
    .button:nth-child(2) {
      position: absolute;
      right: 0;
      transform: skew(-30deg);
      
      .text {
        transform: skew(30deg);
      }
    }
  }
}

.bottom-right {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  font-size: 200%;

  &:hover {
    
    .github {
      height: 20%;
    }
    
    .contact {
      height: 20%;
    }
  }
  
  .background {
    position: absolute;
    width: 100%;
    height: 190%;
  }
  
  .github {
    position: relative;
    height: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    
    &:hover {
      height: 80%;
  
      .button {
        background: #3339; 
        transform: skew(30deg);
        
        .text {
          transform: skew(-30deg);
        }
      }
    }
    
    .background {
      bottom: 0;
      left: 0;
      background-color: gold;
      z-index: -1;
    }

    .button {
      padding: 1px 11px;
      cursor: pointer;
    }
  }
  
  .contact {
    position: relative;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    &:hover {
      height: 80%;

      .number {
        opacity: 1;
        margin-top: 10px;
      }
    }
    
    .background {
      top: 0;
      left: 0;
      background-color: #03a08e;
      z-index: -1;
    }

    .number {
      opacity: 0;
      margin-top: -40px;
    }
  }
}

@media (max-width: 1000px) {
  .container {
    max-width: 100vw;
    height: 140vh;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}

@media (max-width: 630px) {
  .container {
    max-width: 100vw;
    height: 140vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  }

  .item {
    background-repeat: no-repeat;
    background-position: fixed;
    background-attachment: center;
    background-size: 10%;
    
    &:hover {
      .button-panel {
        width: 190px;
        margin-top: 25px;
        opacity: 1;
      }
    }
    
    .back-image {
      display: none;
    }
    
    .title {
      font-size: 222%;
      color: #222d;
      z-index: 1;
    }
    
    .button-panel {
      z-index: 2;
      position: relative;
      width: 170px;
      margin-top: 25px;
      opacity: 1;
    }
  }

  .bottom-right {
    flex-direction: row;

    &:hover {
      
      .github {
        height: 100%;
      }
      
      .contact {
        height: 100%;
      }
    }
    
    .github {
      height: 100%;
      flex-grow: 1;
      
      .button {
        background: #3339; 
        transform: skew(30deg);
        padding: 1px 11px;
        cursor: pointer;
        
        .text {
          transform: skew(-30deg);
        }
      }
    }
    
    .contact {
      position: relative;
      height: 100%;
      flex-grow: 1;
      
      .number {
        opacity: 1;
        margin-top: 10px;
      }
    }
  }
}